import FileItem from "jx/editor/editionitems/FileItem";
export default class ImageSequenceItem extends FileItem {
    constructor(dataObject) {
        dataObject.data.asset.type = "ImageSequence";
        super(Object.assign({
            icoSymbol: "film",
            actions: ["preview"],
            informations: ["asset"],
            backgroundColor: "#cddcf1"
        }, dataObject));
    }
}
